  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');

section{
width: 100%;    
}

.slider-section{
    width: 100%;
    height: 100vh;
}
article{
    position: absolute;
    width: 100%;
    height: 580px;
    transition:  all .8s ease-in-out;
    -webkit-transition:  all .8s ease-in-out;
    -moz-transition:  all .8s ease-in-out;
    -ms-transition:  all .8s ease-in-out;
    -o-transition:  all .8s ease-in-out;
}
article img{
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
article h4{
    font-size: 48px;
}
article button{
    cursor: pointer;
    width: 10rem;
    height: 3rem;
    margin-top: 25px;
    background-color: #2ecc71;
    color: #ffffff;
    font-size: 18px;
    font-weight: 300;
}
article button:hover{
    background-color: #20b861;
}

.text-box{
    position: absolute;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    line-height: 1.5;
    inset: 15%;
    padding: 25px;
    width: auto;
    max-width: 760px;
    height: auto;
    max-height: 450px;
    background-color: #3d3d4e96 ;
    color: #ffffff;
    overflow: hidden;
}
.text-box p {
    font-family: Montserrat, sans-serif;
    font-size: 16px;
    animation-duration: 1000ms;
    animation-delay: 0ms;
    animation-iteration-count: 3;

}
.section-center{
    position: relative;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden
}
.btn{
    cursor: pointer;
    position: absolute;
    padding: 20px 10px 20px 10px;
    color: #ffffff;
    font-size: 40px;
    background: #3d3d4e96;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;


    
}
.next{
    right: 15px;

}
.prev{
    left: 15px ;
}

.next:hover,.prev:hover{
    background: #3d3d4ed3;
}
article.activeSlide{
    opacity: 1;
    transform: translateX(0);
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
}
article.lastSlide{
    transform: translateX(-130%);
    -webkit-transform: translateX(-130%);
    -moz-transform: translateX(-130%);
    -ms-transform: translateX(-130%);
    -o-transform: translateX(-130%);
}
article.nextSlide{
    transform: translateX(130%);
    -webkit-transform: translateX(130%);
    -moz-transform: translateX(130%);
    -ms-transform: translateX(130%);
    -o-transform: translateX(130%);
}
.react-reveal{
    animation-duration: 5000ms;
    animation-delay: 5ms;
    animation-iteration-count: 3;

}

/* responsive */

@media  (max-width:1200px) {
    .text-box{
        width: auto;
        max-width: 600px;
        height: auto;
        max-height: 400px;

    }
    article h4{
        font-size: 36px;
    }
    article button{
        width: 8rem;
        height: 3rem;
    }
    .text-box p {
        font-size: 14px;
    }
    .btn{
        font-size: 20px;

    }
}