@import  url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');

.container{
    width: 100%;
    height: 100vh;
    background-image: url(../images/child-care-template-light-purple-wave-shape-bg.svg);
    background-position: left top;
    background-repeat: no-repeat;
    background-size: contain;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    align-items: center;
    overflow: hidden;

}
.flex-items{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
}
.tablet-image img{
    width: auto;
    max-width: 600px;
    height: auto;
    max-height: 300px;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
}
