.header{
    position: relative;
    height: auto;
    margin-left: 8%;
    background-image: url(../images/family.jpg);
    background-repeat: no-repeat;
    background-size: 50vw;
    background-position: top right;
}
.mainDiv{
    height: 100%;
}
.navbar{
    display: flex;
    align-items: center;

}
.logo{
    width: 160px;
    max-width: 160px;
}
.items{
    display: flex;
    list-style: none;
}

.items li {
    padding:50px 20px 15px 20px;
    cursor: pointer;
    color: #76778b;
}
.items li:hover {
    color: #000;
    transition: all 0.2s;
    -webkit-transition: all 0.2s;
    -moz-transition: all 0.2s;
    -ms-transition: all 0.2s;
    -o-transition: all 0.2s;
}
.text{
    margin-top: 5%;
    color: #6369D1;
    text-transform: uppercase;
    letter-spacing: 2px;

}
.boldText{
    font-family: a;
    margin-top: 5%;
    color: #1d1f45;
    font-size: 4em;
    letter-spacing: 5px;
    line-height: 2em;
    
}
.contactBtn{
    cursor: pointer;
    outline: 0;
    border: 0;
    padding: 14px 30px 14px 30px;
    border-radius: 15px;
    background-color: #545acb;
    color: #ffffff;
    font-weight: bold;
    -webkit-border-radius: 8px;

}
.contactBtn:hover{
    background-color: #1d1f45;
}
.moreBtn{
    cursor: pointer;
    outline: 0;
    border: 0;
    padding: 14px 30px 14px 30px;
    color: #545acb;
    background-color: #ffffff;
    border-radius: 8px;
    transition: all 0.4s ease;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    animation: all 0.2s ease-in;
    -webkit-animation: all 0.2s ease-in;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
}
.moreBtn svg{
    width: 25px;
    margin-left: 10px;
    vertical-align: middle;
}

.moreBtn:hover{
    font-weight: bold;
    transition: all 0.4s ease;
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition-duration: 0.2s;
}

                                                                           /* responsive  */
@media (max-width:1200px){
    .boldText{
        font-size: 3rem;
    }
}
@media (max-width:400px){
    .header{
        height: 90vh;
    }
    .text{
        margin-top: 35%;
    }    
    .boldText{
        font-size: 1.5rem;
    }
    .logo{
        width: 120px;
    }
    
}

